
body {
  font-family: monospace;
}
li {
  margin: 1em 0;
}
footer {
  margin-top: 2em;
}
@media screen and (min-width: 680px) {
footer {
      position: fixed;
      bottom: 0;
      left: 0;
      padding: 1em;
}
}

.hud {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}
@media screen and (min-width: 680px) {
.hud {
      display: flex;
      justify-content: space-between;
}
.hud > * {
        flex: 1 1 0;
}
}

.warning {
  color: #fa0;
}
.danger {
  color: #c00;
}

.progress:after {
  content: '...';
  animation: progress 1.5s linear infinite;
  display: inline;
}
@keyframes progress {
0% {
    content: '.';
}
33.333% {
    content: '..';
}
66.666% {
    content: '...';
}
}

.action {
  margin: 0;
}
